





































































import * as echarts from '../../components/uni/echarts/echarts.simple.min.js';
import mpvueEcharts from '../../components/uni/mpvue-echarts/src/echarts.vue';
var chartData = [1,1];
var chartDate = [1,1];
let lineOption = {
		tooltip: {
		trigger: 'axis',
		position: function (pt) {
			return [pt[0], '10%'];
		},
		axisPointer: {
			type: 'cross',
			label: {
				color: '#fff',
				backgroundColor: 'rgba(0,0,0,0.5)'
			},
			lineStyle: {
				color: 'rgba(0,0,0,0.5)'
			},
			crossStyle: {
				color: 'rgba(0,0,0,0.5)'
			},
		}
	},
	grid: {
		right: 10,
		top: 10,
	},
	xAxis: {
		type: 'category',
		boundaryGap: false,
		data: chartDate,
		axisLine: {
			lineStyle: {
				color: '#fff',
			}
		}
	},
	yAxis: {
		type: 'value',
		boundaryGap: [0, '100%'],
		axisLine: {
			lineStyle: {
				color: '#fff',
			}
		}
	},
	dataZoom: [{
		type: 'inside',
		start: 80,
		end: 100
	}, {
		start: 0,
		end: 10,
		handleIcon: 'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
		handleSize: '80%',
		handleStyle: {
			color: '#fff',
			shadowBlur: 3,
			shadowColor: 'rgba(0, 0, 0, 0.6)',
			shadowOffsetX: 2,
			shadowOffsetY: 2
		}
	}],
	series: [
		{
			type:'line',
			smooth: true,
			symbol: 'none',
			sampling: 'average',
			animation: false,
			itemStyle: {
				color: '#fff'
			},
			data: chartData,
			markPoint: {
				label: {
					color: '#333',
				},
				data: [
					{type: 'max', name: '最大值'},
					{type: 'min', name: '最小值'}
				]
			},
			markLine: {
				lineStyle: {
					color: '#fff'
				},
				data: [
					{type: 'average', name: '平均值'}
				]
			}
		}
	]
};
export default {
	data() {
		const currentDate = this.getDate({
			format: true
		})
		
		return {
			chart:{
				echarts: echarts,
			},
			propertyId: NaN,
			tagId: NaN,
			history: false,
			historyMode: false,
			scrolling:0,
			date_start: currentDate,
			time_start: '00:00',
			date_end: currentDate,
			time_end: '23:59',
			time_same1: '23:59', 
			time_same2: '00:00', 
			
		}
	},
	onLoad: function(e){
		let that = this;
		chartData = []
		chartDate = []
		lineOption.series[0].data = chartData;
		lineOption.xAxis.data = chartDate;
		console.log(e);
		this.tagId = e.propertyId;
		this.machineId = e.machineId;
		uni.setNavigationBarTitle({
			title: e.propertyName,
		});
		uni.closeSocket();
	},
	onReady: function(){
		let that = this;
		setTimeout(function(){
			that.getProperty_now();
		},500)
	},
	onShow: function(){},
	onHide: function(){
		uni.closeSocket();
	},
	onUnload: function(){
		uni.closeSocket();
	},
	updated: function(){},
	beforeUpdate: function(){},
	//下拉动作
	onPullDownRefresh: function(){
		let that = this;
		uni.closeSocket();
		setTimeout(function(){
			that.onSocket();
			uni.stopPullDownRefresh();
			uni.showToast({
				title: '刷新成功',
				duration: 2000,
				icon: "success",
			});
		},2000)	
	},
	//上拉触底
	onReachBottom: function(){},
	//滚动
	onPageScroll: function(e){
		console.log(e)
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	methods: {
		onSocket: function(){
			let that = this;
			uni.connectSocket({
				url: 'wss://38798879.xyz/Extension/GSocket/WSHandler.ashx'
			});
			uni.onSocketOpen(function (res) {
				console.log('参数WebSocket连接已打开！');
				that.getProperty_new();
				that.getNewProperty();
			});
			uni.onSocketClose(function (res) {
				console.log('参数WebSocket 已关闭！');
			});
		},
		chartHistory: function(){
			this.history = true;
		},
		closeHistory: function(){
			this.history = false;
		},
		closeHistoryMode: function(){
			this.closeHistory();
			this.historyMode = false;
			this.getProperty_now();
			// that.$refs.lineChart.init();
		},
		openHistoryMode: function(){
			this.historyMode = true;
			uni.closeSocket();
			this.getProperty_history();
			this.closeHistory();
		},
		lineInit : function(canvas, width, height) {
			let lineChart = echarts.init(canvas, null, {
				width: width,
				height: height
			});
			canvas.setChart(lineChart);
			lineChart.setOption(lineOption);
			return lineChart
		},
		switch2Max : function(e){
			console.log(e)
			if(e.detail.value){
				// lineOption.series.markPoint
			}else{
				
			}
		},
		switch2Min : function(e){
			console.log(e)
		},
		switch2Avg : function(e){
			console.log(e)
		},
		bindDateChange_start: function(e) {
			this.date_start = e.target.value;
			this.time_start= '00:00',
			this.time_end= '23:59',
			this.sameDate_to_setTime();
		},
		bindTimeChange_start: function(e) {
			this.time_start = e.target.value;
			this.sameDate_to_setTime();
		},
		bindDateChange_end: function(e) {
			this.date_end = e.target.value;
			this.time_end= '23:59',
			this.sameDate_to_setTime();
		},
		bindTimeChange_end: function(e) {
			this.time_end = e.target.value;
			this.sameDate_to_setTime();
		},
		sameDate_to_setTime: function(){
			if(this.date_start == this.date_end){
				this.time_same1 = this.time_end;
				this.time_same2 = this.time_start;
			}else{
				this.time_same1 = '23:59';
				this.time_same2 = '00:00';
			}
		},
		getDate: function(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		getProperty_now: function(){
			let that = this;
			chartData = [];
			chartDate = [];
			wx.request({
				url: 'https://38798879.xyz/api/Machine/GetLatestRTTags', 
				data: {
					"Inputs": [
						{
							"GetTagsType": 0,
							"MachineId": that.machineId,
							"TagId": that.tagId,
							"QueryType": 0,
							"StatisticType": 0,
							"StartTime": '',
							"EndTime": '',
							"Count": 200
						}
					]
				},
				method: 'POST',
				success: (res) => {
					console.log(res);
					chartData = [];
					chartDate = [];
					for(let i in res.data[0].Tags){
						chartData.push(res.data[0].Tags[i].FValue.toFixed(2));
						chartDate.push(res.data[0].Tags[i].TimeStamp);
					}
					lineOption.series[0].data = chartData;
					lineOption.xAxis.data = chartDate;
					that.$refs.lineChart.init();
					that.onSocket();
				},
				fail: (res) => {
					console.log(res);
				}
			});
		},
		getProperty_history: function () {
			let that = this;
			let startTimer = that.date_start+' '+that.time_start+':00';
			let endTimer = that.date_end+' '+that.time_end+':00';
			console.log(startTimer);
			chartData = [];
			chartDate = [];
			wx.request({
				url: 'https://38798879.xyz/api/Machine/GetHtyRTTags', 
				data: {
					"Inputs": [
						{
							"GetTagsType": 0,
							"MachineId": that.machineId,
							"TagId": that.tagId,
							"QueryType": 0,
							"StatisticType": 0,
							"StartTime": startTimer,
							"EndTime": endTimer,
							"Count": 200
						}
					]
				},
				method: 'POST',
				success: (res) => {
					console.log(res);
					for(let i in res.data[0].Tags){
						chartData.push(res.data[0].Tags[i].FValue.toFixed(2));
						chartDate.push(res.data[0].Tags[i].TimeStamp);
					};
					lineOption.series[0].data = chartData;
					lineOption.xAxis.data = chartDate;
					that.$refs.lineChart.init();
				},
				fail: (res) => {
					console.log(res);
				}
			});
		},
		getProperty_new: function(e) {
			let that = this;
			uni.onSocketMessage(function (res) {
				let data = JSON.parse(res.data);
				console.log(data);
				chartData.push(data.Data.FValue.toFixed(2));
				chartDate.push(data.Data.TimeStamp);
				lineOption.series[0].data = chartData;
				lineOption.xAxis.data = chartDate;
				that.$refs.lineChart.init();
			});
		},
		getNewProperty: function(e) {
			  let that = this;
			  let machineId = parseInt(that.machineId);
			  let tagId = parseInt(that.tagId);
			  let data = {
				Key: 1,
				Data: {
				  Tags: [{
					MachineId: machineId,
					TagId: tagId
				  }],
				},
			  };
			  console.log(JSON.stringify(data));
			  if (machineId && tagId) {
				setTimeout(function() {
					uni.sendSocketMessage({
						data: JSON.stringify(data)
					});
				}, 500)
			  }
		},
	},
	components: {
		mpvueEcharts
	},
}
